<template>
  <div class="mainApp" :data-them="currentThem">
    <dialog :open="openWindow" v-if="globData.jsonData" ref="RefDialog">
      <Home v-show="is_txt_prompt_page" :pdata="globData.txt_prompt"
        :graioDoms="{ 'txtdom': graioDoms.txt.dom, 'ntxtdom': graioDoms.ntxt.dom }">
        <div class="main-head">
          <div></div>
          <div> <span @click="changThem(item)" class="color" v-for="item, index in themCssArr" :key="index"
                      :style="{ background: item.bgcolor }"></span></div>
        </div>


      </Home>
      <Home pagetit="" v-show="!is_txt_prompt_page" :pdata="globData.img_prompt"
        :graioDoms="{ 'txtdom': graioDoms.img2.dom, 'ntxtdom': graioDoms.nimg2.dom }">
      </Home>

    </dialog>
  </div>


</template>


<script setup>
import { onMounted, ref, watchEffect, watch } from 'vue'
import Home from "../src/components/Home.vue"
import { globStore } from '@/stores/index.js'



const store = globStore()
const { globData } = store
let openWindow = ref(false);

let is_txt_prompt_page = ref(true);

let currentThem = ref('');
let themCssArr = [
  { cssThem: 'default', bgcolor: '#f97316' },
  { cssThem: 'pink', bgcolor: '#E06C75' },
  { cssThem: 'blue', bgcolor: '#5DADE2' },
  { cssThem: 'green', bgcolor: '#238636' },
  { cssThem: 'yellow', bgcolor: '#FF9800' },
]



const graioDoms = ref({
  txt: { query: '#txt2img_prompt textarea', dom: null },
  ntxt: { query: '#txt2img_neg_prompt textarea', dom: null },
  img2: { query: '#img2img_prompt textarea', dom: null },
  nimg2: { query: '#img2img_neg_prompt textarea', dom: null },
  pagetxt: { query: '#tab_txt2img', dom: null },
  pageimg2: { query: '#tab_img2img', dom: null },

});


function changThem(item) {
  currentThem.value = item.cssThem;
  document.documentElement.setAttribute('data-them', item.cssThem)
  window.localStorage.setItem('currentThem', item.cssThem)
}


function initWindow() {
  document.addEventListener('keydown', function (event) {
    if (event.altKey && event.key.toLowerCase() === 'q') {
      openWindow.value = !openWindow.value;
    }

  });
}

function getGraioDoms() {
  for (let [key, value] of Object.entries(graioDoms.value)) {
    value.dom = document.querySelector(value.query)
  }

}





function watchPageChange(dom, callback) {
  const observer = new MutationObserver(mutations => {
    mutations.forEach(mutation => {
      if (mutation.attributeName === 'style') {
        const dom = mutation.target;
        const displayValue = window.getComputedStyle(dom).display;
        if (displayValue === 'block') {
          callback && callback()
        }
        // callback&&callback()
      }
    });
  });
  observer.observe(dom, { attributes: true, attributeFilter: ['style'] });

}



function getJSonData() {
  fetch('api/sixgod/getJsonFiles').then(res => res.json()).then(data => {
    globData.jsonData = JSON.parse(data);
    globData.jsonFileNames = Object.keys(globData.jsonData);

    globData.jsonFileNames.forEach(fileName => {
      globData.cssList[fileName] = 0
    })
  })
}


onMounted(() => {


  // json----------------
  // let jsnfiles = getJsonFiles();

  currentThem.value=window.localStorage.getItem('currentThem')||'default'
  getJSonData();

  // --------------------------------------

  initWindow()
  getGraioDoms()

  watchPageChange(graioDoms.value.pagetxt.dom, () => {
    is_txt_prompt_page.value = true;
    globData.is_txt_prompt_page = true;

  })
  watchPageChange(graioDoms.value.pageimg2.dom, () => {
    is_txt_prompt_page.value = false;
    globData.is_txt_prompt_page = false;

  })






})














</script>




<style scoped>
dialog {
  background-color: var(--body-bg-color);
  color: var(--txt-front-color);
  font-size: var(--base-fontsize);
  width: 100%;
  top: 0;
  left: 0;
  z-index: 999;
  min-height: 200vh;
  padding: 2em;
  box-sizing: border-box;
  margin: 0;
  border: 0px;


}

.color {
  width: 30px;
  height: 30px;
  display: inline-block;
  border-radius: 50%;
  cursor: pointer;
  margin: 0 3px;
}

.main-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1em;
}
</style>
